.app-body > div:nth-of-type(1) {
  width: 10rem;
  outline: 1px solid red;
}
.app-body > div:nth-of-type(2) {
  width: 100%;
}
.App-header {
  background-color: hsla(220, 13%, 18%, 1);
  background-color: var(--gunmetal);
  box-shadow: hsla(220, 13%, 18%, 1) 3px 0px 5px 5px;
  box-shadow: var(--gunmetal) 3px 0px 5px 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
